import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import { Link } from 'react-router-dom';
import kate from './kate.jpg'
import { Stack } from '@mui/material';
import myimg from './myimg.jpeg'



const pages = ['Products', 'Pricing', 'Blog'];
const settings = ['Profile', 'Account', 'Dashboard', 'Logout'];

function Navbar() {
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const [anchorElUser, setAnchorElUser] = React.useState(null);

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    const handleCloseUserMenu = () => {
        setAnchorElUser(null);
    };

    return (
        <AppBar position="static">
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    {/* <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} /> */}
                    <Typography
                        variant="h6"
                        noWrap
                        component="a"
                        href="/"
                        sx={{
                            mr: 2,
                            display: { xs: 'none', md: 'flex' },
                            fontFamily: 'monospace',
                            fontWeight: 700,
                            letterSpacing: '.3rem',
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                        PORTFOLIO
                    </Typography>

                    <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                        >


                            <MenuItem onClick={handleCloseNavMenu}>
                                <Stack direction={'column'}>
                                    <Link to="/" className='decoration'>
                                        <Button
                                            onClick={handleCloseNavMenu}
                                            sx={{ color: 'dark', display: 'block', textDecoration: "none" }} >
                                            Home
                                        </Button>
                                    </Link>
                                    <Link to="/contact" className='decoration' >
                                        <Button
                                            onClick={handleCloseNavMenu}
                                            sx={{ color: 'dark', display: 'block', textDecoration: "none" }} >
                                            Contact
                                        </Button>
                                    </Link>
                                    <Link to="/about" className='decoration'>
                                        <Button
                                            onClick={handleCloseNavMenu}
                                            sx={{ color: 'dark', display: 'block', textDecoration: "none" }} >
                                            About
                                        </Button>
                                    </Link>
                                    <Link to="/portfolio" className='decoration'>
                                        <Button
                                            onClick={handleCloseNavMenu}
                                            sx={{ color: 'dark', display: 'block', textDecoration: "none" }} >
                                            Portfolio
                                        </Button>
                                    </Link>
                                </Stack>

                            </MenuItem>

                        </Menu>
                    </Box>
                    <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
                    <Typography
                        variant="h5"
                        noWrap
                        component="a"
                        href=""
                        sx={{
                            mr: 2,
                            display: { xs: 'flex', md: 'none' },
                            flexGrow: 1,
                            fontFamily: 'monospace',
                            fontWeight: 700,
                            letterSpacing: '.3rem',
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                        PORTFOLIO
                    </Typography>
                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>


                        <Link to="/" className='decoration'>
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                Home
                            </Button>
                        </Link>
                        <Link to="/contact" className='decoration' >
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                Contact
                            </Button>
                        </Link>
                        <Link to="/about" className='decoration'>
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                About
                            </Button>
                        </Link>
                        {/* <Link to="/services" className='decoration'>
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                Services
                            </Button>
                        </Link> */}
                        <Link to="/portfolio" className='decoration'>
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                Portfolio
                            </Button>
                        </Link>
                        {/* <Link to="/contact-info" className='decoration'>
                            <Button
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'white', display: 'block', textDecoration: "none" }} >
                                Contact Info
                            </Button>
                        </Link> */}

                    </Box>

                    <Box sx={{ flexGrow: 0 }}>
                        <Tooltip >
                            <IconButton sx={{ p: 0 }}>
                                <Avatar alt="Akanksha Ambre" src='' />
                            </IconButton>
                        </Tooltip>
                        <Menu
                            sx={{ mt: '45px' }}
                            id="menu-appbar"
                            anchorEl={anchorElUser}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={Boolean(anchorElUser)}
                            onClose={handleCloseUserMenu}
                        >

                            {/* <MenuItem >

                            </MenuItem> */}

                        </Menu>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar >
    );
}
export default Navbar;